<template>
  <!--     -->
  
        <!--     -->

 <div class="project-list">
   <h2> </h2>
   <div class="tabs">
    <router-link class="nav-button active" :to="{ name: 'RecommendationProposal' }"> </router-link>
           <router-link class="nav-button" :to="{ name: 'ActivityPlan' }"> </router-link>
           <router-link class="nav-button" :to="{ name: 'Presentation' }"> </router-link>
           <router-link class="nav-button" :to="{ name: 'FinalReport' }"> </router-link>
           <router-link class="nav-button" :to="{ name: 'Code' }"></router-link>
   </div>
   <table class="project-table">
     <thead>
       <tr>
         <th></th>
         <th></th>
         <th></th>
         <th></th>
         <th></th>
         <th></th>
       </tr>
     </thead>
     <tbody>
       <!--     -->
       <tr v-for="project in projects" :key="project.id" @click="goToDetail(project.id)">
         <td>{{ project.id }}</td>
         <td>{{ project.term }}</td>
         <td>{{ project.title }}</td>
         <td>{{ project.date }}</td>
         <td>{{ project.author }}</td>
         <td>{{ project.views }}</td>
       </tr>
     </tbody>
   </table>
   <div class="pagination">
     <span>1</span>
   </div>
  </div>
<!-- footer   -->
</template>
 
<script setup>
  import { ref, onMounted } from 'vue';
  import axios from 'axios';
  import { useRouter } from 'vue-router';
  import { useUserStore } from '@/store/SignUp';
 
  
  const router = useRouter();
  const userStore = useUserStore();

  
  const currentPage = ref(1);
  const totalPages = ref(1);
  const posts = ref([]);
  
  const fetchPosts = async () => {
    try {
      const response = await axios.get('http://localhost:80/posts', {
        params: { page: currentPage.value }
      });
      posts.value = response.data.posts;
      totalPages.value = response.data.totalPages;
    } catch (error) {
      console.error('Error fetching posts:', error);
    }
  };
  
  const logout = () => {
    userStore.isLoggedIn = false;
    userStore.isAdmin = false;
    router.push({ name: 'Home' });
  };
  
  //   
  onMounted(() => {
    fetchPosts();
  });
  </script>

<style scoped>
.project-list {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}
h2 {
  text-align: center;
}
.tabs {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}
.tabs button {
  margin: 0 0.5rem;
  padding: 0.5rem 1rem;
  border: 1px solid #ddd;
  background-color: #f8f9fa;
}
.tabs button:hover {
  background-color: #e2e6ea;
}
.project-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
}
.project-table th, .project-table td {
  padding: 0.75rem;
  border: 1px solid #ddd;
  text-align: center;
}
.project-table tr:hover {
  background-color: #f1f1f1;
  cursor: pointer;
}
.pagination {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}
</style>