<template>
  <div class="outer-container">
    <div class="inner-container">
      <!--    -->
      <div class="d-flex w-100 align-items-center justify-content-between">
        <div class="d-flex align-items-center">
          <router-link class="navbar-brand" :to="{ name: 'Home' }">
            <img src="@/assets/logo.png" class="logo" alt="" />
          </router-link>
        </div>
        <div>
          <div v-if="userStore.isLoggedIn">
            <p>, {{ userStore.username }}!</p>
            <button @click="logout" class="btn btn-outline-danger ms-3">
              
            </button>
          </div>
          <div v-else>
            <router-link class="nav-link" :to="{ name: 'SignUp' }"
              ></router-link
            >
            <div class="divider"></div>
            <router-link class="nav-link" :to="{ name: 'Login' }"
              ></router-link
            >
          </div>
        </div>
      </div>

      <!--   -->
      <header>
        <div class="lnb">
          <ul>
            <!--  ... -->
          </ul>
        </div>
      </header>

      <!--    -->
      <div class="post-detail" v-if="post.title">
        <h1>{{ post.title }}</h1>
        <p>{{ post.content }}</p>
        <div v-if="post.image">
          <img
            class="post-image"
            :src="`http://localhost:80/uploads/${post.image}`"
            alt=" "
          />
        </div>
        <div v-else>
          <p> </p>
        </div>
        <div v-if="isAuthor">
          <button @click="editPost" class="btn btn-primary"></button>
          <button @click="deletePost" class="btn btn-danger"></button>
        </div>
        <div>
          <textarea
            v-model="comment"
            placeholder=" ..."
            rows="4"
          ></textarea>
          <button @click="submitComment" class="btn btn-primary">
             
          </button>
        </div>
        <div class="comments">
          <h2> </h2>
          <div
            v-for="comment in comments"
            :key="comment.id"
            class="comment-item"
          >
            <p>{{ comment.content }}</p>
            <p>
              <strong>:</strong> {{ comment.username }} |
              <strong>:</strong> {{ formatDate(comment.createdAt) }}
            </p>
            <button
              v-if="comment.authorId === userStore.id"
              @click="deleteComment(comment.id)"
              class="btn btn-danger btn-sm"
            >
              
            </button>
          </div>
        </div>
      </div>
      <div v-else>
        <p>  ...</p>
      </div>

      <!--  -->
      <div class="foote">
        <img src="@/assets/logo2.png" class="logo2" alt="2" />
        <div class="policy-links">
          <a href="#"></a>
          <span>|</span>
          <a href="#"></a>
          <span>|</span>
          <a href="#"></a>
          <span>|</span>
          <a href="#"></a>
        </div>
        <div class="d-flex contact-inf">
          <p>: 02-6343-5700</p>
          <p>: 0506-070-0170</p>
          <p>: info@kead.or.kr</p>
          <p>:     101</p>
        </div>
        <div class="social-links">
          <a href="#"><img src="" alt="Facebook" /></a>
          <a href="#"><img src="" alt="Instagram" /></a>
          <a href="#"><img src="" alt="YouTube" /></a>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, computed } from 'vue';
import axios from 'axios';
import { useRoute, useRouter } from 'vue-router';
import { useUserStore } from '@/store/SignUp';
import { storeToRefs } from 'pinia';

const route = useRoute();
const router = useRouter();
const userStore = useUserStore();
const { isLoggedIn, id: userId } = storeToRefs(userStore);

const post = ref({
  title: '',
  content: '',
  id: 0,
  userId: 0,
  image: '', //   
});
const comments = ref([]);
const comment = ref('');
const isAuthor = computed(
  () => isLoggedIn.value && post.value.userId === userId.value
);

const fetchPostDetails = async () => {
  try {
    const response = await axios.get(
      `http://localhost:80/posts/${route.params.id}`
    );
    post.value = response.data;
    console.log('Post Data:', post.value); //   

    const commentsResponse = await axios.get(
      `http://localhost:80/posts/${route.params.id}/comments`
    );
    console.log('Comments Data:', commentsResponse.data);
    comments.value = commentsResponse.data;
  } catch (error) {
    console.error('Error fetching post details:', error);
  }
};

const editPost = () => {
  router.push({
    name: 'EditRecruitmentNotice',
    params: { id: route.params.id },
  });
};

const deletePost = async () => {
  if (!confirm('   ?')) return;

  try {
    await axios.delete(`http://localhost:80/posts/${post.value.id}`);
    router.push({ name: 'RecruitmentNotice' }); //    
  } catch (error) {
    console.error('Error deleting post:', error);
    alert('    .');
  }
};

const submitComment = async () => {
  if (!comment.value.trim()) {
    alert('   .');
    return;
  }

  try {
    await axios.post(`http://localhost:80/posts/${post.value.id}/comments`, {
      content: comment.value,
      authorId: userId.value, //   ID
    });
    comment.value = ''; //    
    fetchPostDetails(); //   
  } catch (error) {
    console.error('Error submitting comment:', error);
    alert('    .');
  }
};

const deleteComment = async (commentId) => {
  try {
    await axios.delete(
      `http://localhost:80/posts/${post.value.id}/comments/${commentId}`
    );
    fetchPostDetails(); //   
  } catch (error) {
    console.error('Error deleting comment:', error);
    alert('    .');
  }
};

const formatDate = (dateString) => {
  const date = new Date(dateString);
  return date.toLocaleDateString() + ' ' + date.toLocaleTimeString();
};

const logout = () => {
  userStore.isLoggedIn = false;
  userStore.isAdmin = false;
  router.push({ name: 'Home' });
};

//   
onMounted(() => {
  fetchPostDetails();
});
</script>

<style scoped>
/*    */
.post-detail {
  margin-top: 20px;
}

.comment-item {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  position: relative;
}

textarea {
  width: 100%;
  margin-top: 10px;
}

.post-image {
  width: 100%; /*      */
  max-width: 600px; /*   600px  */
  height: auto; /*      */
}

.btn-danger {
  position: absolute;
  top: 10px;
  right: 10px;
}

.comments h2 {
  margin-top: 20px;
  margin-bottom: 10px;
}

.comment-item p {
  margin: 0;
  padding: 0;
}

.comment-item .btn-danger {
  display: block;
  margin-top: 10px;
}
</style>
